export default function NetworkingPage() {
  return (
    <div className="max-w-4xl mx-auto px-8 py-12">
      <h1 className="text-4xl font-bold mb-4 dark:text-white">Networking</h1>
      <p className="text-xl text-gray-600 dark:text-gray-300 mb-8">
        Setup for consistent behaviour while being portable.
      </p>

      <div className="space-y-8">
        <section>
          <h2 className="text-2xl font-semibold mb-4 dark:text-white">Network Topology</h2>
          <div className="bg-gray-100 dark:bg-gray-800 rounded-lg p-6">
            <pre className="text-sm overflow-x-auto">
{`┌─────────────────────────────────────┐
│     Home Router (DHCP Server)        │
│         192.168.1.1/24               │
└──────────────┬──────────────────────┘
               │
     ┌─────────┴─────────┐
     │  Gigabit Switch    │
     └─────────┬──────────┘
               │
     ┌─────────┴──────────────────────┐
     │         │         │             │
┌────┴───┐ ┌──┴────┐ ┌──┴────┐ ┌─────┴──┐
│ Master │ │Worker1│ │Worker2│ │Worker3 │
│  Node  │ │ Node  │ │ Node  │ │  Node  │
└────────┘ └───────┘ └───────┘ └────────┘`}
            </pre>
          </div>
        </section>

        <section>
          <h2 className="text-2xl font-semibold mb-4 dark:text-white">Static IP Configuration</h2>
          <div className="overflow-x-auto">
            <table className="min-w-full bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg">
              <thead className="bg-gray-50 dark:bg-gray-700">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">Node</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">Hostname</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">IP Address</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">Role</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200 dark:divide-gray-700">
                <tr>
                  <td className="px-6 py-4 whitespace-nowrap dark:text-gray-300">Node 1</td>
                  <td className="px-6 py-4 whitespace-nowrap dark:text-gray-300">k3s-master</td>
                  <td className="px-6 py-4 whitespace-nowrap font-mono text-sm dark:text-gray-300">192.168.1.101</td>
                  <td className="px-6 py-4 whitespace-nowrap"><span className="px-2 py-1 bg-purple-100 dark:bg-purple-900 text-purple-800 dark:text-purple-200 rounded text-xs">Master</span></td>
                </tr>
                <tr>
                  <td className="px-6 py-4 whitespace-nowrap dark:text-gray-300">Node 2</td>
                  <td className="px-6 py-4 whitespace-nowrap dark:text-gray-300">k3s-worker-1</td>
                  <td className="px-6 py-4 whitespace-nowrap font-mono text-sm dark:text-gray-300">192.168.1.102</td>
                  <td className="px-6 py-4 whitespace-nowrap"><span className="px-2 py-1 bg-green-100 dark:bg-green-900 text-green-800 dark:text-green-200 rounded text-xs">Worker</span></td>
                </tr>
                <tr>
                  <td className="px-6 py-4 whitespace-nowrap dark:text-gray-300">Node 3</td>
                  <td className="px-6 py-4 whitespace-nowrap dark:text-gray-300">k3s-worker-2</td>
                  <td className="px-6 py-4 whitespace-nowrap font-mono text-sm dark:text-gray-300">192.168.1.103</td>
                  <td className="px-6 py-4 whitespace-nowrap"><span className="px-2 py-1 bg-green-100 dark:bg-green-900 text-green-800 dark:text-green-200 rounded text-xs">Worker</span></td>
                </tr>
                <tr>
                  <td className="px-6 py-4 whitespace-nowrap dark:text-gray-300">Node 4</td>
                  <td className="px-6 py-4 whitespace-nowrap dark:text-gray-300">k3s-worker-3</td>
                  <td className="px-6 py-4 whitespace-nowrap font-mono text-sm dark:text-gray-300">192.168.1.104</td>
                  <td className="px-6 py-4 whitespace-nowrap"><span className="px-2 py-1 bg-green-100 dark:bg-green-900 text-green-800 dark:text-green-200 rounded text-xs">Worker</span></td>
                </tr>
              </tbody>
            </table>
          </div>
        </section>

        <section>
          <h2 className="text-2xl font-semibold mb-4 dark:text-white">Key Network Features</h2>
          <div className="grid md:grid-cols-2 gap-4">
            <div className="border border-gray-200 dark:border-gray-700 rounded-lg p-4">
              <h3 className="font-semibold mb-2 dark:text-white">🔒 Isolated Network</h3>
              <p className="text-sm text-gray-600 dark:text-gray-400">Dedicated switch for cluster traffic isolation</p>
            </div>
            <div className="border border-gray-200 dark:border-gray-700 rounded-lg p-4">
              <h3 className="font-semibold mb-2 dark:text-white">📍 Static IPs</h3>
              <p className="text-sm text-gray-600 dark:text-gray-400">Consistent addressing for reliable communication</p>
            </div>
            <div className="border border-gray-200 dark:border-gray-700 rounded-lg p-4">
              <h3 className="font-semibold mb-2 dark:text-white">🚀 Gigabit Speed</h3>
              <p className="text-sm text-gray-600 dark:text-gray-400">1Gbps networking for fast data transfer</p>
            </div>
            <div className="border border-gray-200 dark:border-gray-700 rounded-lg p-4">
              <h3 className="font-semibold mb-2 dark:text-white">🔌 Portable Setup</h3>
              <p className="text-sm text-gray-600 dark:text-gray-400">Easy to move and reconnect anywhere</p>
            </div>
          </div>
        </section>
      </div>
    </div>
  )
}
